const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {};
	},

	methods: {
		outLogin() {
			
			uni.showModal({
				title: '提示',
				content: '是否退出系统',
				success: res => {
					if (res.confirm) {
						// console.log('用户点击确定');
						this.runOutLogin()
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
			
		},
		runOutLogin() {
			// 执行用户登出
			// uni.showLoading({
			//   title: '执行中...'
			// });
			uni.showLoading({
			  title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/logout`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.data.state_code == 1) {
						uni.reLaunch({
							url: `/pages/login`
						})
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
				}
			})
		}
	}
};
